<template lang="pug">
    v-card
        v-toolbar(flat)
            v-toolbar-title 产量分级图
        v-divider
        div(ref="gradePieElement" class="gradePieClass")
            VegaChartView(
                :key="chartId"
                :chartSource="GradePieData.GradePieData"
                :id="chartId"
                :chartId="chartIdStr"
                :chartNum="chartNum"
            )
        
            
                
</template>

<script>

import { get } from 'vuex-pathify'
import GradePieData from "./GradePieData.js";
export default {
  name: "GradePie",
  
  data: () => ({
        GradePieData: GradePieData,
        chartNum:0,
        chartIdStr:"#GradePie",
        chartId:"GradePie",
  }),
  computed:{
      screenWidth:get("app/screenWidth"),
    //   chartId(){
    //       let cid = Math.random().toString(36).slice(-8);
    //       this.setChartId(cid);
    //       return cid 
    //   }
  },
  methods:{
      setChartId(){
          let cid = Math.random().toString(36).slice(-8);
          this.chartId=cid;
          this.chartIdStr="#"+cid;
          console.log("GradePie ",this.chartId,this.chartIdStr);
      },
      chart(){
          
          console.log("宽度:",this.$refs.gradePieElement.clientWidth);
          this.GradePieData.GradePieData.width=this.$refs.gradePieElement.clientWidth-150;
          this.chartNum=this.chartNum+1;
      }
  },
    watch:{
        screenWidth(){
            console.log("窗口变化");
            this.setChartId();
            setTimeout(this.chart,2000);
        }
    },
    components: {
      VegaChartView: () => import('@/views/charts/vegacharts/VegaChartView'),
    },

};
</script>

<style>
.gradePieClass{
    width:100%
}
</style>